
import { defineComponent, ref } from "vue";
import { Router, useRouter } from "vue-router"

export default defineComponent({
    name: "Loading",
    setup() {
        const router:Router = useRouter()
        let ratio = ref(0)
        let timer: number;
        timer = setInterval(() => {
            if (ratio.value == 100) {
                router.push("/create-scene");
                clearInterval(timer);
                return
            }
            ratio.value += 1
        }, 10)
        return {
           ratio 
        }
    },
})
